.container {
  display: grid;
  place-items: center center;
}

#main-container {
  display: grid;
  grid-template-rows: 80px 1fr;
  height: 100vh;
}

.parent-div {
  background-color: white;
  width: 20rem;
  height: 20rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  border-radius: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.89);
  -webkit-box-shadow: 1px 3px 15px -5px rgba(0, 0, 0, 0.89);
  -moz-box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.89);
}

.child-div-one {
  background-color: white;
  grid-column: 2/4;
  grid-row: 1/3;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 25px;
}

.child-div-two {
  grid-row: 3/4;
  grid-column: 1/5;
  display: flex;
  justify-content: center;
  align-items: center;
}

.child-div-three {
  grid-row: 4;
  grid-column: 1/5;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-bottom: 25px;
}

button {
  border-radius: 10px;
  width: 50px;
  height: 50px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.89);
  -webkit-box-shadow: 1px 3px 15px -5px rgba(0, 0, 0, 0.89);
  -moz-box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.89);
  border-color: #ffffff;
  background-color: white;
  font-size: 2rem;
}

button:hover {
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.89);
  -webkit-box-shadow: 1px 3px 15px 1px rgba(0, 0, 0, 0.89);
  -moz-box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.89);
}

#input-number {
  width: 100px;
  height: 100px;
  font-size: 4rem;
  border: 1px solid #999;
  border-radius: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.89);
  -webkit-box-shadow: 1px 3px 15px -5px rgba(0, 0, 0, 0.89);
  -moz-box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.89);
  outline: none;
  border-color: #ffffff;
  text-align: center;
}

#input-number:hover {
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.89);
  -webkit-box-shadow: 1px 3px 15px 1px rgba(0, 0, 0, 0.89);
  -moz-box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.89);
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.number-show {
  font-size: 2rem;
}

#title {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: skyblue;
}

body {
  padding: 0;
  margin: 0;
  box-sizing: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}